.App {
  font-family: sans-serif;
  text-align: center;
  width: 288px;
  height: 600px;
  margin: 0 auto;
  padding: 24px;
  border-radius: 16px;
  border: 1px solid red;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex.column {
  flex-direction: column;
}

.buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 8px;
  margin-top: 24px;
}

textarea {
  width: 270px;
  height: 320px;
  background: rgba(115, 229, 81, 1);
  font-size: 24px;
  font-family: monospace;
  border-radius: 8px;
  padding: 8px;
  resize: none;
}

button.clear {
  margin-top: 8px;
  width: 100%;
}

button {
  text-transform: uppercase;
}
